.loading {
    width: 100vw;
    height:100vh;
    background-color: white;
    display: grid;
    place-items: center;
    max-width: 100%;
} 

.loading > div {
    width: 10vmax;
    height: 10vmax;
    border-bottom: 5px solid #712cf9;
    border-radius: 50%;
    animation: loadingRotate 700ms linear infinite;
}

@keyframes loadingRotate {
    to {
        transform: rotateZ(-360deg);
    }
}